import React from 'react';
import { Formik } from 'formik';
import { object, string } from 'yup';

import Input from './Input';
import TextArea from './TextArea';
import Error from './Error';
import Button from './Button';

export default function ContactSalesForm({ error, onSubmit }) {
  return (
    <div className="contact_sales_form__form-container">
      <Formik
        initialValues={{
          name: '',
          email: '',
          phone: '',
          company: '',
          website: '',
          message: '',
        }}
        validationSchema={object().shape({
          name: string().trim().required('Required'),
          email: string()
            .trim()
            .lowercase()
            .email('Invalid email')
            .required('Required'),
          company: string().trim().required('Required'),
          website: string().trim().lowercase(),
          message: string().trim().required('Required'),
        })}
        onSubmit={onSubmit}
      >
        {({
          values,
          errors,
          isSubmitting,
          handleChange,
          handleBlur,
          handleSubmit,
        }) => (
          <form onSubmit={handleSubmit}>
            <Input
              id="name"
              name="name"
              placeholder="Name"
              error={errors.name}
              value={values.name}
              onChange={handleChange}
              onBlur={handleBlur}
            />
            <Input
              id="email"
              name="email"
              placeholder="Work email"
              error={errors.email}
              value={values.email}
              onChange={handleChange}
              onBlur={handleBlur}
            />
            <Input
              id="phone"
              name="phone"
              type="tel"
              placeholder="Phone number"
              error={errors.phone}
              value={values.phone}
              onChange={handleChange}
              onBlur={handleBlur}
            />
            <Input
              id="company"
              name="company"
              type="text"
              placeholder="Company name"
              error={errors.company}
              value={values.company}
              onChange={handleChange}
              onBlur={handleBlur}
            />
            <Input
              id="website"
              name="website"
              type="text"
              placeholder="Company website"
              error={errors.website}
              value={values.website}
              onChange={handleChange}
              onBlur={handleBlur}
            />
            <TextArea
              id="message"
              placeholder="Type your message..."
              error={errors.message}
              value={values.message}
              onChange={handleChange}
              onBlur={handleBlur}
            />
            <Error
              error={
                error &&
                'There was an error submitting your request. Please try again.'
              }
            />
            <Button
              type="submit"
              primary
              disabled={isSubmitting}
              className="contact_sales_form__submit"
            >
              Submit
            </Button>
          </form>
        )}
      </Formik>
      <style jsx global>{`
        .contact_sales_form__submit {
          width: 100%;
        }
      `}</style>
    </div>
  );
}
