import React, { useState } from 'react';
import { graphql } from 'gatsby';

import Layout from '../components/Layout';
import ContactSalesForm from '../components/ContactSalesForm';
import Footer from '../components/Footer';
import BlockLogos from '../components/BlockLogos';

import { fonts } from '../constants';
import { edgesByName } from '../utils/gql';

export const pageQuery = graphql`
  {
    companies: allFile(filter: { relativeDirectory: { eq: "companies" } }) {
      edges {
        node {
          name
          childImageSharp {
            fluid {
              base64
              tracedSVG
              srcWebp
              srcSetWebp
              originalImg
              originalName
            }
          }
        }
      }
    }
  }
`;

export default function ContactSalesPage({ data }) {
  const [error, setError] = useState(null);
  const [showSuccess, setShowSuccess] = useState(false);

  const companies = edgesByName(data.companies);

  async function onSubmit(values, { setSubmitting, resetForm }) {
    try {
      setError(null);
      const host =
        window.location.hostname === localhost
          ? 'https://app.resolvepay.com'
          : 'http://localhost:2222';

      const res = await fetch(`${host}/api/web/contact`, {
        headers: {
          'Content-Type': 'application/json',
        },
        method: 'POST',
        body: JSON.stringify(values),
      }).then(body => body.json());

      if (res && res.success) {
        resetForm();
        setShowSuccess(true);
      }
    } catch (err) {
      setError(err);
    } finally {
      setSubmitting(false);
    }
  }

  return (
    <Layout>
      <div className="contact_sales__container">
        <div className="contact_sales__title">See Resolve in action</div>
        {showSuccess ? (
          <div>
            <p className="contact_sales__subtitle">
              Thanks! We'll get back to you soon
            </p>
          </div>
        ) : (
          <div>
            <p className="contact_sales__subtitle">
              Tell us how to reach you and let's discuss how Resolve can grow
              your b2b business.
            </p>
            <ContactSalesForm error={error} onSubmit={onSubmit} />
          </div>
        )}
      </div>
      <BlockLogos
        data={[
          {
            image: companies['cozyearth'].originalImg,
            name: 'CozyEarth',
            offsetTop: 0,
          },
          {
            image: companies['hyperikon'].originalImg,
            name: 'Hyperikon',
          },
          {
            image: companies['interior-define'].originalImg,
            name: 'Interior Define',
            offsetTop: 0,
          },
          {
            image: companies['simpletire-grey'].originalImg,
            name: 'SimpleTire',
            offsetTop: 0,
          },
          {
            image: companies['realthread'].originalImg,
            name: 'Real Thread',
            offsetTop: -6,
          },
        ]}
      />
      <Footer />
      <style jsx>{`
        .contact_sales__container {
          width: 450px;
          margin: 50px auto;
        }
        .contact_sales__title {
          font-family: ${fonts.gilroy};
          font-size: 28px;
          font-weight: 600;
          line-height: 1.43;
          letter-spacing: 0px;
          text-align: center;
        }
        .contact_sales__subtitle {
          font-family: ${fonts.halyardDisplay};
          font-size: 22px;
          line-height: 1.64;
          letter-spacing: 0.8px;
          margin: 20px 0 40px;
          text-align: center;
        }
      `}</style>
    </Layout>
  );
}
